body {
  background: #1b1e22;
}

.jackpot-round {
  margin-left: 10px;
  max-height: 500px;
  overflow-y: auto;

  &-info {
    border-radius: 8px;
    padding: 10px 15px;
    background-color: #1b1e22;
  }

  &-player {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    padding: 3px 12px;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    border-radius: 8px;
    position: relative;
    margin: 15px 0;
    cursor: pointer;

    &-highlight {
      position: absolute;
      bottom: -3px;
      left: 5%;
      right: 5%;
      height: 3px;
      border-bottom-left-radius: 12px;
      border-bottom-right-radius: 12px;
    }

    &-separator {
      width: 2px;
      -ms-flex-item-align: center;
      -ms-grid-row-align: center;
      align-self: center;
      height: 60px;
      background-image: linear-gradient(transparent, #323438, transparent);
      margin: 0 10px;
    }

    &-info {
      span {
        color: #fff;
        font-size: 15px;
      }
      h3 {
        font-size: 18px;
      }
    }

    .jackpot-round-box {
      -ms-flex-item-align: center;
      align-self: center;
      -webkit-box-flex: 1;
      -ms-flex: 1;
      flex: 1;
      color: white;
    }

    &-deposit {
      text-align: right;
      span {
        font-size: 17px;
        color: #67696b;
      }

      h4 {
        font-size: 21px;
        color: #dfdfdf;
      }
    }
  }

  &-winner {
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    padding: 8px 12px;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    border-radius: 8px;
    position: relative;
    margin: 20px 3px 5px;
    overflow: hidden;
    cursor: pointer;

    &-user {
      -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
      flex: 0 0 auto;
      -ms-flex-item-align: center;
      align-self: center;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;
      span {
        color: #c5cdd0;
        font-size: 17px;
      }
      h3 {
        color: #fcfcfc;
        font-size: 21px;
        font-weight: 700;
      }
    }

    &-nouser {
      -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
      flex: 0 0 auto;
      -ms-flex-item-align: center;
      align-self: center;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;

      span {
        color: #fcfcfc;
        font-size: 21px;
        font-weight: 700;
      }
    }

    &-info {
      color: #fcfcfc;
      padding: 6px 0;
      -ms-flex: 0 0 auto;
      flex: 0 0 auto;
      -ms-flex-item-align: center;
      -ms-grid-row-align: center;
      align-self: center;
      -webkit-box-flex: 1;
      -ms-flex: 1;
      flex: 1;

      h2 {
        font-size: 19px;
        line-height: 19px;
        font-weight: 700;
      }
      span {
        font-size: 14px;
      }
    }
  }

  &-no-winner {
  }
}

.jackpot-roulette {
  position: relative;
  text-align: center;

  &-spinner {
    position: absolute;
    top: 50%;
    left: 50%;
    border-radius: 50%;
    transition: all 5s;
    background-repeat: no-repeat;
    background-position: top;
    background-size: contain;
    z-index: 1;
    img {
      width: 100%;
      margin-top: -7.5%;
      // padding-left: 8px;
      // padding-right: 8px;
    }
  }

  &-info {
    text-align: center;
    position: absolute;
    top: 50%;
    border: 1px solid #1a1b20;
    left: 50%;
    border-radius: 50%;
    background: repeating-linear-gradient(135deg, #1c1f24, #1c1f24 20px, #202328 20px, #202328 40px) #202328;
    z-index: 2;

    &-total,
    &-state {
      padding: 5vmin;

      span {
        color: #494b4f;
        font-size: 1.5vmin;
        letter-spacing: 2px;
      }
      h3 {
        color: #fff;
        font-weight: 700;
        font-size: 3.4vmin;
        line-height: 3.4vmin;
      }
    }
  }
}
